import Vue from 'vue';
import httpResource from 'vue-resource';

import $ from 'jquery'

import error from './components/error-toast';
import map from './components/map';
import list from './components/list';

import headerSection from './components/header-section';
import landingSection from './components/landing-section';
import infoSection from './components/info-section';
import supportSection from './components/support-section';
import footerSection from './components/footer-section';

import jquery from 'jquery'
import onLoad from './modules/index'

require('./scss/main.scss');

// add event listeners to the onload function
onLoad();

// add plugins to Vue
Vue.use( httpResource );


// adding components
Vue.component( 'error', error )
Vue.component( 'map', map )
Vue.component( 'list', list )

// adding sections
Vue.component( 'landing-section', landingSection )
Vue.component( 'info-section', infoSection )
Vue.component( 'support-section', supportSection )
Vue.component( 'footer-section', footerSection )
Vue.component( 'header-section', headerSection )

var App = new Vue({

	////////////////////////////////////////////////////////////////////////
	/// init
	////////////////////////////////////////////////////////////////////////
	el: '#app',
	data: {
		user: {},
		zipcode: '',
		error: '',
		offices: [],
		loaded: '',
		loading: '',
		showMore: false,
		language: 'english',

		formText: {
			title: '',
			placeholder: '',
			cta: '',
		},

		spanish: {
			title: 'Llama hoy para comenzar.',
			placeholder: 'Escribe tu código postal',
			cta: 'Encuentra oficinas',
			seeMoreButton: 'Más información',
			listHeader: "Llama a una oficina de WIC cerca de"
		},

		english: {
			title: "Call today to get started.",
			placeholder: 'Enter Zipcode',
			cta: 'Find Locations',
			seeMoreButton: 'See More',
			listHeader: "Call a WIC office near"
		}

	},

	created: function() {
		//update the language paramater
		var urlParams;
		(window.onpopstate = function () {
		    var match,
		        pl     = /\+/g,  // Regex for replacing addition symbol with a space
		        search = /([^&=]+)=?([^&]*)/g,
		        decode = function (s) { return decodeURIComponent(s.replace(pl, " ")) },
		        query  = window.location.search.substring(1)

		    urlParams = {}
		    while (match = search.exec(query))
		       urlParams[decode(match[1])] = decode(match[2])
		})()

		// Set Language
		var language= ( urlParams.lang === 'es') ? 'spanish' : 'english';
		this.language = language;
		this.langParam =  (this.language === 'spanish') ? 'es' : 'en';
		this.formText.title = this[language].title;
		this.formText.placeholder = this[language].placeholder;
		this.formText.cta = this[language].cta;
		this.seeMoreButton = this[language].seeMoreButton;
		this.listHeader = this[language].listHeader;
		this.trackingPixelsSet = false;
		
	},

	////////////////////////////////////////////////////////////////////////
	// events
	////////////////////////////////////////////////////////////////////////
	events: {

		updateMapTrigger: function( activeOffice ) {
			this.$broadcast( 'updateMap', activeOffice );
		}

	},
	////////////////////////////////////////////////////////////////////////
	// methods
	////////////////////////////////////////////////////////////////////////
	methods: {

		revealContent() {
			console.log('removal')
			console.log($('.vue-hidden'))
			$('.vue-hidden').removeClass('vue-hidden')
			this.showMore = true;
		},

		setTrackingPixels( event ) {

			// Google Analytics
			if (window.ga) {
				ga('send', 'event', 'Requested Offices', this.zipcode);
			}

			var head = document.getElementsByTagName('head')[0],
				body = document.getElementsByTagName('body')[0];

			// Accordant
			var Accordant = document.createElement('script');
			Accordant.src = 'https://secure.adnxs.com/px?id=779324&seg=6713598&order_id=[' + this.zipcode + ']&t=1';
			head.appendChild(Accordant);

			// Programatic Mechanics
			var ProgrammaticMechanics1 = new Image(),
			    ProgrammaticMechanics2 = new Image(),
			    ProgrammaticMechanics3 = new Image();

			ProgrammaticMechanics1.src = 'https://secure.adnxs.com/px?id=772745&value=[' + this.zipcode + ']&t=2';
			ProgrammaticMechanics2.src = 'https://ad.doubleclick.net/ddm/activity/src=6258079;type=invmedia;cat=qntxrmge;dc_lat=;dc_rdid=;tag_for_child_directed_treatment=;ord=1?';
			ProgrammaticMechanics3.src = 'https://sp.analytics.yahoo.com/spp.pl?a=10000&.yp=10017676&ev=Conv';

			body.appendChild(ProgrammaticMechanics1);
			body.appendChild(ProgrammaticMechanics2);
			body.appendChild(ProgrammaticMechanics3);

			// Rocket Fuel
			var RocketFuel = document.createElement('script');
			RocketFuel.setAttribute('async', 'true');
			RocketFuel.setAttribute('src', '//c1.rfihub.net/js/tc.min.js');
			head.appendChild(RocketFuel);
			if (typeof window['_rfi'] !== 'function') {
				window['_rfi']=function() {
					window['_rfi'].commands = window['_rfi'].commands || [];
					window['_rfi'].commands.push(arguments);
				};
			}
			_rfi('setArgs', 'ver', '9');
			_rfi('setArgs', 'rb', '28807');
			_rfi('setArgs', 'ca', '20755918');
			_rfi('setArgs', '_o', '28807');
			_rfi('setArgs', '_t', '20755918');
			_rfi('setArgs', 'ZIP CODE', this.zipcode);
			_rfi('track');

			// Floodlight
			var axel = Math.random() + "";
			var a = axel * 10000000000000;
			// document.write('<img src="https://ad.doubleclick.net/ddm/activity/src=6256945;type=creat0;cat=signu0;dc_lat=;dc_rdid=;tag_for_child_directed_treatment=;ord=' + a + '?" width="1" height="1" alt=""/>');

			var Floodlight = new Image();
			Floodlight.src = 'https://ad.doubleclick.net/ddm/activity/src=6256945;type=creat0;cat=signu0;dc_lat=;dc_rdid=;tag_for_child_directed_treatment=;ord=' + a + '?';
			body.appendChild(Floodlight);

		},

		getData( event ) {
			this.showMore = false;

			event.preventDefault();
			
			this.error = ''

			this.loading = true
			
			let key = encodeURI( 'zipcode' )
			let value = encodeURI( this.zipcode )

			if ( value.length === 5 ) {
				value = encodeURI( this.zipcode )
			} else {
				value = this.zipcode.replace(/ /g,"+")
			}

			if ( this.zipcode.trim() === '' ) {
				this.loading = false

				if ( this.language === 'spanish' ) {
					this.error = 'Dejaste esto en blanco. Por favor, escribe un código postal de 5 dígitos y envía.'
				} else {
					this.error = 'Oops, You left this blank. Please add a 5 digit zipcode and submit.'	
				}
				
				return;
			}


			var endpoint= `/getData?${key}=${value}&language=${this.langParam}`,
				additionalParams = '';

				
			if (window.location.search) {
				endpoint += '&'+window.location.search.split('?')[1];
			}

			this.$http.post( endpoint ).then( response => {
				
				if ( response.body.error ) {
					this.error = response.body.error
					this.loading = false

					// Google Analytics
					if (window.ga) {
						var eventID = (response.body.eventID) ? response.body.eventID : null;
						ga('send', 'event', 'Office Request Failed', this.zipcode, eventID);
					}

					// Facebook pixel
					if (window.fbq) {
						fbq('track', 'Office Request Failed', {
							eventID: eventID,
							zipcode: value
						});
					}

					return;
				}

				// remove loading
				this.loading = false

				// add items to the data state
				this.user = response.data.user

				//add an index key for google maps
				this.offices = response.data.ordered.map( function( item, index ) {
					var newObj = {};
					newObj = item;
					newObj.key = index;
					return newObj;
				} );

				// set first item to active
				this.offices[0].active = true

				//trigger map  render on map component
				this.$broadcast( 'setMap', this.offices )

				this.loaded = true
				this.zipcode = value

				// Google Analytics
				if (window.ga) {
					var eventID = (response.body.eventID) ? response.body.eventID : null;
					ga('send', 'event', 'Received Offices', value, eventID);
				}

				// Facebook pixel
				if (window.fbq) {
					fbq('track', 'Received Offices', {
						eventID: eventID,
						zipcode: value
					});
				}

				scrollViewList();

			}, ( response ) => {
				console.log( 'theres been an error' )
			});

			return false;
		}

		
	}
});

////////////////////////////////////////////////////////////////////////
// utils
////////////////////////////////////////////////////////////////////////
// the callback for google maps, if we need it
var initMap = function() {
	// silence is golden	
}

function scrollViewList() {
	let listView = document.getElementsByClassName( 'listViewSection' )[0]
	listView.scrollTop = 0
}

////////////////////////////////////////////////////////////////////////
// expose the public methods
////////////////////////////////////////////////////////////////////////

window.initMap = initMap
