import Vue from 'vue';

var headerSection = Vue.extend( {
	template: '#header-section',
	props: ['language'],
	data: function() {
		return {

			cta: '',
			url: '',

			english: {
				cta: 'En español',
				url: '?lang=es'
			},

			spanish: {
				cta: 'In English',
				url: '/'
			}
		}
	},

	created: function() {
		this.cta = this[this.language].cta
		this.url = this[this.language].url
	}
} )

export default headerSection;